package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.CIL
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.HRANA
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.KATEGORIE
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.KONEC
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.POZADI
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.POZIRACI
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.SUPERUZEL_SEPARATOR
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.TEMA
import eu.karelhovorka.textgame.core.exception.StateException


data class SuperStateBuilder(
        val gameConverterContext: GameConverterContext,
        val id: String,
        var currentTheme: String? = null,
        var currentBackground: Array<String> = emptyArray(),
        var currentCategories: Array<String> = emptyArray(),
        var selfConsuming: Boolean? = null,
        var finalTargetId: String? = null,
        var chapter: ChapterBuilder? = null,
        val chapterId: String? = null,
        var tmpStates: MutableList<StateBuilder> = mutableListOf(),
        var endType: EndType = EndType.NONE
) : LineParser {

    var separated = false
    var move = false

    override fun parseLine(line: String, index: Int) {
        if (line.matches(SUPERUZEL_SEPARATOR)) {
            val stateId = addState(line.stateId())
            gameConverterContext.addStateId(stateId, index)
            separated = true
            move = false
        } else if (line.matches(HRANA)) {
            move = true
            currentStateBuilder().parseLine(line, index)
        } else if (line.matches(POZIRACI)) {
            selfConsuming = line.parameterBoolean()
            currentStateBuilder().parseLine(line, index)
        } else if (line.matches(KONEC) && tmpStates.size == 1) {
            endType = endType(line, index)
        } else if (!move && line.matches(CIL)) {
            finalTargetId = line.parameterValue()
        } else if (!move && line.matches(TEMA)) {
            val tema = line.replace(TEMA, "$1")
            currentTheme = tema
            currentStateBuilder().parseLine(line, index)
        } else if (line.matches(KATEGORIE)) {
            currentCategories = line.parameterArray()
            currentStateBuilder().parseLine(line, index)
        } else if (line.matches(POZADI)) {
            currentBackground = line.parameterArray()
            currentStateBuilder().parseLine(line, index)
        } else {
            currentStateBuilder().parseLine(line, index)
        }
    }

    init {
        if (chapterId != null && chapter == null) {
            chapter = ChapterBuilder(id = chapterId)
        }
        addState()
    }

    fun build(next: String? = null): List<State> {
        if (finalTargetId == null && currentStateBuilder().moves.isEmpty() && endType == EndType.NONE && currentStateBuilder().endType == EndType.NONE) {
            throw StateException(id, "Superuzel $id potřebuje cíl, hrany anebo konec, případně poslední uzel superuzlu potřebuje kon")
        }
        if (finalTargetId != null && currentStateBuilder().moves.isNotEmpty()) {
            throw StateException(id, "Superuzel $id může mít buď cíl ($finalTargetId) anebo hrany (${currentStateBuilder().moves.map { it.verbose }}) (ne obojí)")
        }
        val states = mutableListOf<State>()
        var index = 0
        for (stateBuilder in tmpStates) {
            val nextState: StateBuilder? = if (index == tmpStates.size - 1) {
                null
            } else {
                tmpStates[index + 1]
            }
            val nextStateId = if (nextState == null) {
                finalTargetId
            } else {
                nextState.id
            }
            if (stateBuilder.moves.isEmpty() && stateBuilder.endType == EndType.NONE) {
                val moveId = "h_${id}_sub_$index"
                if (nextState == null) {
                    if (finalTargetId != null) {
                        stateBuilder.moves.add(
                                MoveBuilder(
                                        gameConverterContext = gameConverterContext,
                                        id = moveId,
                                        targetId = finalTargetId!!,
                                        selfConsuming = stateBuilder.selfConsuming ?: selfConsuming
                                        ?: MoveBuilder.DEFAULT_SELF_CONSUMING,
                                        background = currentBackground,
                                        categories = currentCategories,
                                        chapter = chapter
                                )
                        )
                    } else if (endType != EndType.NONE) {
                        stateBuilder.endType = endType
                    }
                } else {
                    stateBuilder.moves.add(
                            MoveBuilder(
                                    gameConverterContext = gameConverterContext,
                                    id = moveId,
                                    targetId = nextStateId,
                                    selfConsuming = stateBuilder.selfConsuming ?: selfConsuming
                                    ?: MoveBuilder.DEFAULT_SELF_CONSUMING,
                                    background = currentBackground,
                                    categories = currentCategories,
                                    chapter = chapter
                            )
                    )

                }
            } else {
            }
            val state = if (nextState == null) {
                stateBuilder.build(next)
            } else {
                stateBuilder.build(nextStateId)
            }
            states.add(state)
            index += 1
        }
        return states
    }

    fun currentStateBuilder(): StateBuilder {
        return tmpStates.last()
    }

    fun addState(stateId: String? = null): String {
        var newStateId: String
        if (stateId == null) {
            newStateId = id
            if (tmpStates.isNotEmpty()) {
                newStateId = "${id}_sub_${tmpStates.size}"
            }
        } else {
            newStateId = stateId
        }
        tmpStates.add(StateBuilder(
                gameConverterContext = gameConverterContext,
                id = newStateId,
                chapterId = chapter?.id ?: chapterId,
                theme = currentTheme, background = currentBackground,
                selfConsuming = selfConsuming,
                categories = currentCategories
        ))
        return newStateId
    }
}