package eu.karelhovorka.textgame.core.builder

import kotlinx.js.JsName


class GameConverterContext() {
    val lineOfState: MutableMap<String, Int> = mutableMapOf()
    val lineOfMove: MutableMap<String, Int> = mutableMapOf()

    fun addStateId(state: String, index: Int) {
        lineOfState.put(state, index)
    }

    fun addMoveId(move: String, index: Int) {
        lineOfMove.put(move, index)
    }

    @JsName("stateLine")
    fun stateLine(stateId: String): Int? {
        return lineOfState[stateId]
    }

    @JsName("moveLine")
    fun moveLine(moveId: String): Int? {
        return lineOfMove[moveId]
    }

}

