package eu.karelhovorka.textgame.core.bo

import eu.karelhovorka.textgame.core.variables.Variable
import kotlin.js.JsName


fun Move.toGraphElement(): GraphElement {
    return object : GraphElement {
        override val id: String
            get() = this@toGraphElement.id
        override val text: CharSequence?
            get() = this@toGraphElement.text
        override val flags: Array<String>
            get() = this@toGraphElement.flags
        override val flagsToRemove: Array<String>
            get() = this@toGraphElement.flagsToRemove
        override val categories: Array<String>
            get() = this@toGraphElement.categories
        override val theme: String?
            get() = this@toGraphElement.theme
        override val background: Array<String>
            get() = this@toGraphElement.background
        override val variables: Array<Variable>
            get() = this@toGraphElement.variables
        override val pause: PauseType
            get() = this@toGraphElement.pause
        override val illustration: String?
            get() = this@toGraphElement.illustration
    }
}

fun State.toGraphElement(): GraphElement {
    return object : GraphElement {
        override val id: String
            get() = this@toGraphElement.id
        override val text: CharSequence?
            get() = this@toGraphElement.text
        override val flags: Array<String>
            get() = this@toGraphElement.flags
        override val flagsToRemove: Array<String>
            get() = this@toGraphElement.flagsToRemove
        override val categories: Array<String>
            get() = this@toGraphElement.categories
        override val theme: String?
            get() = this@toGraphElement.theme
        override val background: Array<String>
            get() = this@toGraphElement.background
        override val variables: Array<Variable>
            get() = this@toGraphElement.variables
        override val pause: PauseType
            get() = this@toGraphElement.pause
        override val illustration: String?
            get() = this@toGraphElement.illustration
    }
}

interface GraphElement {
    @JsName("id")
    val id: String
    @JsName("text")
    val text: CharSequence?
    @JsName("flags")
    val flags: Array<String>
    @JsName("flagsToRemove")
    val flagsToRemove: Array<String>
    @JsName("categories")
    val categories: Array<String>
    @JsName("theme")
    val theme: String?
    @JsName("background")
    val background: Array<String>
    @JsName("variables")
    val variables: Array<Variable>
    @JsName("pause")
    val pause: PauseType
    @JsName("illustration")
    val illustration: String?
}