package eu.karelhovorka.textgame.core.bo

import eu.karelhovorka.util.RandomUtil

data class MultiString(val strings: Array<String>, var rnd: Int = 0) : CharSequence {
    override val length: Int
        get() = value().length

    override fun get(index: Int): Char {
        return value().get(index)
    }

    override fun subSequence(startIndex: Int, endIndex: Int): CharSequence {
        return value().subSequence(startIndex, endIndex)
    }

    fun value(): String {
        return strings[rnd]
    }

    fun next() {
        rnd = RandomUtil.nextInt(strings.size)
    }

    override fun toString(): String {
        next()
        return value()
    }

}