package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.GameConfig
import kotlinx.js.JsName


class GameConverter : LineParser {
    var converterContext = GameConverterContext()
    private var currentState: StateBuilder? = null
    private var currentMove: MoveBuilder? = null
    private var currentSuperState: SuperStateBuilder? = null
    private var currentChapter: ChapterBuilder? = null
    private var game = GameConfigBuilder()

    var lineParser: LineParser = this


    @JsName("convert")
    fun convert(text: String): GameConfig {
        this.parse(text)
        if (currentState != null) {
            buildState()
        }
        if (currentMove != null) {
            buildMove()
        }
        if (currentSuperState != null) {
            buildSuperState()
        }
        if (currentChapter != null) {
            buildChapter()
        }
        return game.build()
    }

    override fun parseLine(line: String, index: Int) {
        if (line.startsWith("#")) {
            //ignore
        } else if (line == "" || line == SEPARATOR) {
        } else if (line.matches(SUPERUZEL)) {
            val id = line.parameterValue()
            finishBuild(id)
            if (game.startId == null) {
                game.startId = id
            }
            converterContext.addStateId(id, index)
            currentSuperState = SuperStateBuilder(gameConverterContext = converterContext, id = id, chapterId = currentChapter!!.id)
            lineParser = currentSuperState!!
        } else if (line.matches(KAPITOLA)) {
            finishBuild()
            buildChapter()
            val chapterId = line.parameterValue()
            currentChapter = ChapterBuilder(id = chapterId)
            lineParser = currentChapter!!
        } else if (line.matches(UZEL)) {
            val stateId = line.parameterValue()
            finishBuild(stateId)
            if (game.startId == null) {
                game.startId = stateId
            }
            converterContext.addStateId(stateId, index)
            currentState = StateBuilder(gameConverterContext = converterContext, id = stateId, chapter = currentChapter)
            lineParser = currentState!!
        } else {
            lineParser.parseLine(line, index)
        }
    }

    fun finishBuild(nextId: String? = null) {
        if (lineParser is StateBuilder) {
            buildState(nextId)
        }
        if (lineParser is SuperStateBuilder) {
            buildSuperState(nextId)
        }
    }


    fun buildMove() {
        if (currentMove != null) {
            val move = currentMove!!.build()
            game.moves.add(move)
            currentMove = null
        }
    }

    fun buildChapter() {
        if (currentChapter != null) {
            game.chapters.add(currentChapter!!.build())
            currentChapter = null
        }
    }

    fun buildSuperState(nextId: String? = null) {
        if (currentSuperState != null) {
            val states = currentSuperState!!.build(nextId)
            game.addStates(states)
            currentChapter!!.addStates(states)
            currentSuperState = null
            if (game.startId == null) {
                game.startId = states[0].id
            }
        }
    }

    fun buildState(nextId: String? = null) {
        if (currentState != null) {
            val state = currentState!!.build(nextId)
            game.addState(state)
            currentChapter!!.addState(state)
            currentState = null
            if (game.startId == null) {
                game.startId = currentState!!.id
            }
        }
    }


    @kotlinx.js.JsName("context")
    fun context(): GameConverterContext {
        return converterContext
    }

    companion object {
        val UZEL = ("uzel=(u[0-9A-Za-z_]*)").toRegex()

        val PARAMETER = "([a-z_]+)=(.*)".toRegex()

        val SEPARATOR = "----"

        val UZEL_VSECHNY_START = "(uzel=|superuzel=|-)(u[0-9A-Za-z_]*)?".toRegex()

        val HRANA = "(hrana=|->|<-)(h[0-9A-Za-z_]*|u[0-9A-Za-z_]*)?".toRegex()

        val KAPITOLA = "kapitola=(.*)".toRegex()

        val ILUSTRACE = "ilustrace=(.*)".toRegex()

        val POZADI = "pozadi=(.*)".toRegex()

        val KATEGORIE = "kategorie=(.*)".toRegex()

        val KONEC = "konec=(prohra|nedokonceno)".toRegex()

        val TEMA = "tema=([a-zA-Z\\-0-9_{}]+)".toRegex()

        val SUPERUZEL = "superuzel=(u[a-zA-Z\\-0-9_]+)".toRegex()

        val SUPERUZEL_SEPARATOR = "(-)(u[a-zA-Z\\-0-9_]+)?".toRegex()

        val PRIZNAK = "priznak=(.*)".toRegex()

        val ODSTRANIT_PRIZNAK = "odstranit_priznak=(.*)".toRegex()

        val POZIRACI = "poziraci=(ano|ne)".toRegex()

        val CIL = "cil=(.*)".toRegex()

        val PROMENNA = "promenna\\(([a-z_\\-]+)\\)([+\\-]?=)(.*)".toRegex()

        val PAUZA = "pauza=(ano|ne|auto)".toRegex()
    }
}

