package eu.karelhovorka.textgame.core.bo

import eu.karelhovorka.textgame.core.builder.MoveBuilder
import eu.karelhovorka.textgame.core.variables.Variable

val prefix = "[hu]([^_]+)_.*".toRegex()


fun Collection<Move>.isInteractive(): Boolean {
    return this.any { !it.isAutoMove() }
}

fun Collection<Move>.isNotInteractive(): Boolean {
    return !isInteractive()
}

fun Array<Move>.isInteractive(): Boolean {
    return this.any { !it.isAutoMove() }
}

fun Array<Move>.isNotInteractive(): Boolean {
    return !isInteractive()
}

fun List<Move>.removeNotInteractive(): List<Move> {
    return this.filter { !it.isAutoMove() }
}

fun Move.simpleString(): String {
    return "$id -> $targetId"
}

fun List<Move>.checkMultipleNonInteractive() {
    val auto = this.filter { it.isAutoMove() }.map { it.simpleString() }
    val nonAuto = this.filter { !it.isAutoMove() }
    if (auto.size > 1 && nonAuto.isEmpty()) {
        throw IllegalStateException("Víc neinteraktivních tahů najednou $auto, to je průser")
    }
}

fun Move.flagsOrEmpty(): String {
    if (flags.isEmpty()) {
        return ""
    }
    return flags.toList().toString()
}

data class Move(
        //shared with state
        val id: String,
        val text: String? = null,
        val flags: Flags = emptyArray(),
        val flagsToRemove: Flags = emptyArray(),
        val categories: Array<String> = emptyArray(),
        val theme: String? = null,
        val background: Array<String> = emptyArray(),
        val variables: Array<Variable> = emptyArray(),
        val pause: PauseType = PauseType.AUTO,
        val illustration: String? = null,

        val caption: String? = null,
        val image: String? = null,
        val selfConsuming: Boolean = MoveBuilder.DEFAULT_SELF_CONSUMING,
        val targetId: String,
        val flagsRequired: Array<String> = emptyArray(),
        val flagsDisallowing: Array<String> = emptyArray(),
        val hash: String? = null
) {

    fun usePause(): Boolean {
        return when (pause) {
            PauseType.AUTO ->
                hasContent()
            PauseType.NO ->
                false
            PauseType.YES ->
                true
        }
    }

    fun isAutoMove(): Boolean {
        return caption == null && image == null && hash == null
    }

    fun hasContent(): Boolean {
        return text != null || illustration != null
    }

    fun acrossChapters(): Boolean {
        val movePrefix = id.replace(prefix, "$1")
        val targetPrefix = targetId.replace(prefix, "$1")
        return movePrefix != targetPrefix
    }
}
