package eu.karelhovorka.textgame.core.variables

data class Variable(val name: String, val value: String) {


    companion object {
        val REGEX = "promenna\\(([a-z_\\-]+)\\)([+\\-]?=)(.*)".toRegex()

        fun parse(string: String): Variable {
            if (string.matches(REGEX)) {
                val variableName = string.replace(REGEX, "$1")
                val operator = string.replace(REGEX, "$2")
                var expression = string.replace(REGEX, "$3")
                if (operator == "+=") {
                    expression = "$variableName+$expression"
                } else if (operator == "-=") {
                    expression = "$variableName - $expression"
                } else if (operator == "*=") {
                    expression = "$variableName*$expression"
                } else if (operator == "/=") {
                    expression = "$variableName/$expression"
                }
                return Variable(variableName, expression)
            } else {
                throw IllegalArgumentException("Does not match $string")
            }
        }
    }
}