package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.PauseType
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.HRANA
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.PARAMETER
import eu.karelhovorka.textgame.core.exception.IncorrectLineException
import eu.karelhovorka.textgame.core.exception.StateException
import eu.karelhovorka.textgame.core.validator.MOVE_ID_REGEX
import eu.karelhovorka.textgame.core.variables.Variable
import eu.karelhovorka.util.fixBOM


fun endType(line: String, index: Int): EndType {
    val konec = line.parameterValue()

    return when (konec) {
        "prohra" -> EndType.LOSE
        "nedokonceno" -> EndType.UNFINISHED
        "vyhra" -> EndType.WIN
        else -> {
            throw IncorrectLineException(msg = "Invalid endType: $konec", line = line, index = index)
        }
    }
}

fun pause(line: String, index: Int): PauseType {
    val pause = line.parameterValue()
    return when (pause) {
        "ano" -> PauseType.YES
        "ne" -> PauseType.NO
        "auto" -> PauseType.AUTO
        else -> {
            throw IncorrectLineException(msg = "Invalid pauseType: $pause", line = line, index = index)
        }
    }

}

private fun checkParameter(line: String) {
    if (!line.matches(PARAMETER)) {
        throw RuntimeException("this is not parameter: $line")
    }

}

fun errorIfParameter(line: String, index: Int, msg: String = "Neočekávaný parametr $line, ($index)") {
    if (line.matches(PARAMETER)) {
        throw IncorrectLineException(index, line, msg)
    }
}

fun String.moveId(): String? {
    val id = this.replace(HRANA, "$2")
    if (id.isBlank()) {
        return null
    }
    if (id.matches(MOVE_ID_REGEX)) {
        return id
    }
    return null
}

enum class MoveType {
    LEFT_ARROW, RIGHT_ARROW, MOVE
}

fun String.moveType(): MoveType {
    val type = this.replace(HRANA, "$1")
    return when (type) {
        "->" ->
            MoveType.RIGHT_ARROW
        "<-" ->
            MoveType.LEFT_ARROW
        "hrana=" ->
            MoveType.MOVE
        else -> error("Unknown move type $type")
    }

}

fun String.stateId(): String? {
    val id = this.replace(eu.karelhovorka.textgame.core.builder.GameConverter.SUPERUZEL_SEPARATOR, "$2")
    if (id.isBlank()) {
        return null
    }
    return id
}

fun String.parameterValue(): String {
    if (this.startsWith("->")) {
        return this.replace("(->)(.*)".toRegex(), "$2").fixHtmlEntities()
    } else if (this == "<-") {
        return ""
    }
    checkParameter(this)
    return this.replace(PARAMETER, "$2").fixHtmlEntities()
}

fun String.parameterArray(delimiter: String = ","): Array<String> {
    checkParameter(this)
    val result = this.parameterValue()
    if (result.isBlank()) {
        return emptyArray()
    }
    return result.split(delimiter).map { it.trim() }.filter { it.isNotBlank() }.toTypedArray();
}

fun String.fixHtmlEntities(): String {
    return this.replace("&gt;", ">").replace("&lt;", "<");
}

fun LineParser.parse(text: String) {
    var index = 0
    for (line in text.fixBOM().split("\n")) {
        try {
            val fixedLine = line.trim()
                    .replace("-&gt;", "->")
                    .replace("&lt;-", "<-")
            parseLine(fixedLine, index)
        } catch (e: IncorrectLineException) {
            throw e;
        } catch (e: StateException) {
            throw IncorrectLineException(index, line, msg = e.msg, e = e)
        } catch (e: Exception) {
            throw IncorrectLineException(index, line, e = e)
        }
        index += 1
    }
}

fun String.parameterVariable(): Variable {
    return Variable.parse(this)
}

fun String.parameterBoolean(): Boolean {
    return this.parameterValue().toLowerCase() == "ano"
}