package eu.karelhovorka.textgame.core.validator

import eu.karelhovorka.textgame.core.exception.IncorrectLineException
import kotlinx.js.JsName

val incorrect2 = "osoba=[“„]?([^“]*)“>"
val incorrect2_replacement = "osoba='$1'>"

val incorrect3 = ".*<myslenka [^>]*>[^<]*<myslenka .*".toRegex()

class MyslenkaFixer {

    //val incorrect = ".* osoba=(^\"|^').*".toRegex()
    val incorrect = ".*osoba=[^\"'].*".toRegex()


    @JsName("find")
    fun find(content: String): Array<String> {
        var index = 0
        val lines = mutableListOf<String>()
        for (line in content.split("\n".toRegex())) {
            val newLine = findLine(line, index)
            lines.add(newLine)
            index += 1
        }
        return lines.toTypedArray()
    }

    fun fix(content: String): String {
        var result = content.replace(incorrect2.toRegex(), incorrect2_replacement)
        result = result.replace("osoba=([a-z]*)>".toRegex(), incorrect2_replacement)


        result = result.replace("</primarec osoba=", "<primarec osoba=")
        return result
    }

    fun findLine(line: String, index: Int): String {
        val fixed = fix(line)
        //println(fixed)
        if (fixed.matches(incorrect)) {
            throw IncorrectLineException(index, line)
        }
        return fixed
    }
}
