package eu.karelhovorka.textgame.core.bo

data class Chapter(
        val id: String,
        val name: String,
        val categories: Array<String> = emptyArray(),
        val theme: String? = null,
        val flagGroups: Array<FlagGroup> = emptyArray(),
        val states: Array<State>,
        val testDefaultTheme: String? = null,
        val filePrefix: String? = null
) {
    fun withoutStates(): Chapter {
        return copy(states = emptyArray())
    }
}