package eu.karelhovorka.textgame.core.bo


import eu.karelhovorka.util.itemsHashCode
import kotlinx.serialization.Serializable


interface GameState {
    val flags: Array<String>
    val currentStateId: String
    val variables: Map<String, String>
    val currentTheme: String?
    val visitedChapters: Array<String>
    val timestamp: Long
}

fun GameState.extensionEquals(other: Any?): Boolean {
    if (this === other) return true
    if (other == null || other !is GameState) return false

    if (!flags.contentEquals(other.flags)) return false
    if (currentStateId != other.currentStateId) return false
    if (variables != other.variables) return false
    if (currentTheme != other.currentTheme) return false
    if (!visitedChapters.contentEquals(other.visitedChapters)) return false

    return true
}

fun GameState.extensionHashCode(): Int {
    var result = flags.itemsHashCode()
    result = 31 * result + currentStateId.hashCode()
    result = 31 * result + variables.hashCode()
    result = 31 * result + (currentTheme?.hashCode() ?: 0)
    result = 31 * result + visitedChapters.itemsHashCode()
    return result
}


fun GameState.toImmutable(): ImmutableGameState {
    if (this is ImmutableGameState) {
        return this
    }
    return ImmutableGameState(
            flags = this.flags,
            currentStateId = this.currentStateId,
            variables = this.variables,
            currentTheme = this.currentTheme,
            visitedChapters = this.visitedChapters,
            timestamp = this.timestamp
    )
}

@Serializable
data class ImmutableGameState(
        override val flags: Array<String>,
        override val currentStateId: String,
        override val variables: Map<String, String>,
        override val currentTheme: String?,
        override val visitedChapters: Array<String> = emptyArray(),
        override val timestamp: Long = 0
) : GameState {
    override fun equals(other: Any?): Boolean {
        return extensionEquals(other)
    }

    override fun hashCode(): Int {
        return extensionHashCode()
    }

    override fun toString(): String {
        return "GameState(flags=${flags.toList()}, currentStateId='$currentStateId', variables=$variables, currentTheme=$currentTheme)"
    }


}