package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.PauseType
import eu.karelhovorka.textgame.core.variables.Variable


data class MoveBuilder(
        val gameConverterContext: GameConverterContext,
        var id: String? = null,
        var caption: String? = null,
        var image: String? = null,
        var illustration: String? = null,
        var text: String? = null,
        var selfConsuming: Boolean = DEFAULT_SELF_CONSUMING,
        var targetId: String? = null,
        var flags: MutableList<String> = mutableListOf(),
        var flagsToRemove: MutableList<String> = mutableListOf(),
        var flagsRequired: MutableList<String> = mutableListOf(),
        var flagsDisallowing: MutableList<String> = mutableListOf(),
        var categories: Array<String> = emptyArray(),
        var theme: String? = null,
        var variables: MutableList<Variable> = mutableListOf<Variable>(),
        var background: Array<String> = emptyArray(),
        var pause: PauseType = PauseType.AUTO,
        var hash: String? = null,
        var chapter: ChapterBuilder? = null

) : LineParser {
    override fun parseLine(line: String, index: Int) {
        when {
            line == GameConverter.SEPARATOR -> {
            }
            line.matches(GameConverter.PAUZA) -> {
                pause = eu.karelhovorka.textgame.core.builder.pause(line, index)
            }
            line.matches(GameConverter.PRIZNAK) -> {
                flags = line.parameterArray().toMutableList()
            }
            line.matches(GameConverter.ODSTRANIT_PRIZNAK) -> {
                flagsToRemove = line.parameterArray().toMutableList()
            }
            line.matches(GameConverter.PROMENNA) -> {
                variables.add(line.parameterVariable())
            }
            line.matches(GameConverter.TEMA) -> {
                theme = line.parameterValue()
            }
            line.matches(GameConverter.POZIRACI) -> {
                selfConsuming = line.parameterBoolean()
            }
            line.matches(GameConverter.KATEGORIE) -> {
                categories = line.parameterArray()
            }
            line.matches(GameConverter.ILUSTRACE) -> {
                illustration = line.parameterValue()
            }
            line.matches(OBRAZEK) -> {
                image = line.parameterValue()
            }
            line.matches(VYLOUCENA) -> {
                flagsDisallowing.addAll(line.parameterArray())
            }
            line.matches(PODMINENA) -> {
                flagsRequired.addAll(line.parameterArray())
            }
            line.matches(POPIS) -> {
                caption = line.parameterValue()
            }
            line.matches(GameConverter.POZADI) -> {
                background = line.parameterArray()
            }
            line.matches(HASH) -> {
                hash = line.parameterValue()
            }
            line.matches(GameConverter.CIL) -> {
                targetId = line.parameterValue()
            }
            line == "-" -> {
                //
            }
            else -> {
                errorIfParameter(line, index)
                appendText(line)
            }
        }
    }

    fun build(): Move {
        if (id == null) {
            throw IllegalArgumentException("Id not found for move!")
        }
        if (targetId == null) {
            throw IllegalArgumentException("Target it not found for move ${id}!")
        }
        return Move(
                id = id!!,
                caption = caption,
                image = chapter.addPrefixIfNotNull(image),
                illustration = chapter.addPrefixIfNotNull(illustration),
                text = text?.trim(),
                selfConsuming = selfConsuming,
                targetId = targetId!!,
                flags = flags.toTypedArray(),
                flagsToRemove = flagsToRemove.toTypedArray(),
                flagsRequired = flagsRequired.toTypedArray(),
                flagsDisallowing = flagsDisallowing.toTypedArray(),
                categories = categories,
                theme = theme,
                variables = variables.toTypedArray(),
                background = chapter.addPrefixIfNotNull(background),
                pause = pause,
                hash = hash?.toUpperCase()
        )
    }

    fun appendText(value: String) {
        if (text == null) {
            text = value + "\n"
        } else {
            text += value + "\n"
        }
    }

    val verbose: String
        get() {
            val caption: String = id ?: caption ?: image ?: ""
            return "$caption -> $targetId"
        }

    companion object {
        val DEFAULT_SELF_CONSUMING = true

        val OBRAZEK = "obrazek=(.*)".toRegex()

        val POPIS = "popis=(.*)".toRegex()

        val HASH = "hash=(.*)".toRegex()

        val VYLOUCENA = "vyloucena=(.*)".toRegex()

        val PODMINENA = "podminena=(.*)".toRegex()

    }
}