package eu.karelhovorka.textgame.core.bo

import eu.karelhovorka.textgame.core.builder.Selection
import eu.karelhovorka.textgame.core.variables.Variable


fun State.flagsOrEmpty(): String {
    if (flags.isEmpty()) {
        return ""
    }
    return flags.toList().toString()
}

data class State(
        //shared with move
        val id: String,
        val text: CharSequence? = null,
        val flags: Flags = emptyArray(),
        val flagsToRemove: Flags = emptyArray(),
        val theme: String? = null,
        val background: Array<String> = emptyArray(),
        val variables: Array<Variable> = emptyArray(),
        val pause: PauseType = PauseType.AUTO,
        val illustration: String? = null,

        val chapterId: String,
        val moves: Array<Move> = emptyArray(),
        val categories: Array<String> = emptyArray(),
        val endType: EndType = EndType.NONE,
        val minigame: String? = null,
        var checkpoint: String? = null,
        val selection: Selection = Selection.NONE
) {

    fun withoutMoves(): State {
        return copy(moves = emptyArray())
    }

    fun hasContent(): Boolean {
        return !(text == null && illustration == null && background == null)
    }

    fun usePause(filteredMoves: Array<Move> = moves): Boolean {
        return when (pause) {
            PauseType.AUTO ->
                hasContent() && filteredMoves.isNotInteractive()
            PauseType.NO ->
                false
            PauseType.YES ->
                true
        }
    }

    val verbose: String
        get() {
            return "$chapterId - $id"
        }

}