package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.*

data class GameConfigBuilder(
        var startId: String? = null,
        var states: MutableList<State> = mutableListOf(),
        var moves: MutableList<Move> = mutableListOf(),
        var chapters: MutableList<Chapter> = mutableListOf()
) {
    val gameConverterContext = GameConverterContext()

    fun build(): GameConfig {
        return GameConfig(
                startId = startId!!,
                states = states.toTypedArray(),
                moves = moves.toTypedArray(),
                chapters = chapters.toTypedArray()
        )
    }

    fun addState(state: State) {
        states.add(state)
        moves.addAll(state.moves)
    }

    fun addStates(newStates: Collection<State>) {
        states.addAll(newStates)
        newStates.forEach {
            moves.addAll(it.moves)
        }

    }

    fun state(init: StateBuilder.() -> Unit): State {
        val stateBuilder = StateBuilder(gameConverterContext = gameConverterContext, categories = emptyArray<String>())
        stateBuilder.init()
        val state = stateBuilder.build()
        addState(state)
        return state
    }

    fun move(init: MoveBuilder.() -> Unit): Move {
        val moveBuilder = MoveBuilder(gameConverterContext = gameConverterContext)
        moveBuilder.init()
        val move = moveBuilder.build()
        moves.add(move)
        return move
    }

    fun chapter(init: ChapterBuilder.() -> Unit): Chapter {
        val moveBuilder = ChapterBuilder()
        moveBuilder.init()
        val move = moveBuilder.build()
        chapters.add(move)
        return move
    }

}
