package eu.karelhovorka.textgame.core.util

import eu.karelhovorka.textgame.core.bo.*
import eu.karelhovorka.util.addUnique
import eu.karelhovorka.util.getOrThrow

class GameConfigHelper(val gameConfig: GameConfig) {
    private val statesById = gameConfig.states.associateBy { it.id }
    private val movesById = gameConfig.moves.associateBy { it.id }
    private val chaptersById = gameConfig.chapters.associateBy { it.id }
    private val movesByTargetId = gameConfig.moves.groupBy { it.targetId }
    private val flagGroupsByStateId = mutableMapOf<String, MutableList<FlagGroup>>()
    private val flagGroups = chaptersById.map { it.value.flagGroups }.flatMap {
        it.asIterable()
    }

    init {
        for (state in gameConfig.states) {
            if (flagGroupsByStateId.get(state.id) == null) {
                flagGroupsByStateId[state.id] = mutableListOf()
            }
            for (move in state.moves) {
                for (flag in move.flagsDisallowing) {
                    val flagGroup = isInGroup(state.chapterId, flag)
                    if (flagGroup != null) {
                        flagGroupsByStateId.get(state.id)!!.add(flagGroup)
                    }
                }
                for (flag in move.flagsRequired) {
                    val flagGroup = isInGroup(state.chapterId, flag)
                    if (flagGroup != null) {
                        flagGroupsByStateId.get(state.id)!!.add(flagGroup)
                    }
                }
            }
        }
    }

    fun stateById(id: String): State {
        return statesById.getOrThrow(id)
    }

    fun start(): State {
        return stateById(gameConfig.startId)
    }

    fun moveById(id: String): Move {
        return movesById.getOrThrow(id)
    }

    fun movesOf(state: State): Array<Move> {
        return state.moves
    }

    fun containsState(stateId: String): Boolean {
        for (state in gameConfig.states) {
            if (state.id == stateId) {
                return true
            }
        }
        return false
    }

    fun containsMove(moveId: String): Boolean {
        for (move in gameConfig.moves) {
            if (move.id == moveId) {
                return true
            }
        }
        return false
    }

    fun moveExists(from: String, to: String): Boolean {
        if (!statesById.containsKey(from)) {
            return false
        }
        val state = statesById.getOrThrow(from)

        for (move in state.moves) {
            if (movesById.containsKey(move.id)) {
                if (move.targetId == to) {
                    return true
                }
            }
        }
        return false
    }

    fun pathExists(from: String, to: String): Boolean {
        if (from == to) {
            return true
        }
        if (to == gameConfig.startId) {
            return false
        }
        val moves = movesByTargetId.getOrThrow(to)
        for (state in gameConfig.states) {
            for (move2 in state.moves) {
                for (move in moves) {
                    if (move2.id == move.id) {
                        return pathExists(from, state.id)
                    }
                }
            }
        }
        throw IllegalArgumentException("No parent found for moves ${moves.map { it.id }}")
    }

    fun isInGroup(chapterId: String, flag: String): FlagGroup? {
        for (flagGroup in flagGroups) {
            if (flag in flagGroup.flags) {
                return flagGroup
            }
        }
        return null
    }

    fun chapterById(chapterId: String): Chapter {
        return chaptersById.getOrThrow(chapterId)
    }

    fun flagGroupsOf(state: State): List<FlagGroup> {
        return flagGroupsByStateId.getOrThrow(state.id)
    }

    fun printFlagGroups() {
        for (state in gameConfig.states) {
            val moves = state.moves
            val flags = mutableSetOf<String>()
            for (move in moves) {
                flags.addAll(move.flagsDisallowing)
                flags.addAll(move.flagsRequired)
            }
            if (flags.size > 1) {
                val flagsStr = flags.joinToString(",")
                println("skupina(${state.id}-skupina,prave_jeden)=$flagsStr")
            }
        }
    }

    fun printImageAssets() {
        val results = mutableListOf<String>()
        for (state in gameConfig.states) {
            if (state.illustration != null) {
                results.addUnique(preload(PathConstants.ILLUSTRATIONS + state.illustration))
            }
            for (background in state.background) {
                results.addUnique(preload(PathConstants.BACKGROUNDS + background))
            }
        }
        for (move in gameConfig.moves) {
            if (move.illustration != null) {
                results.addUnique(preload(PathConstants.ILLUSTRATIONS + move.illustration))
            }
        }
        for (move in gameConfig.moves) {
            if (move.image != null) {
                results.addUnique(preload(PathConstants.MOVES + move.image))
            }
        }
        results.forEach {
            println(it)
        }
    }

    fun preload(path: String): String {
        //TODO: {{tema}}
        return "preload.loadFile('$path')"
    }

    fun printFlags() {
        println("Stavy")
        for (state in gameConfig.states) {
            if (state.flags.isNotEmpty()) {
                println("${state.id} - ${state.flags.toList()}")
            }
        }
        println("Hrany")
        for (move in gameConfig.moves) {
            if (move.flags.isNotEmpty()) {
                println("${move.id} - ${move.flags.toList()}")
            }
        }
    }
}