package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.*
import eu.karelhovorka.textgame.core.exception.IncorrectLineException

fun ChapterBuilder?.addPrefixIfNotNull(filename: String?): String? {
    if (filename == null) {
        return null
    }
    if (this?.filePrefix == null) {
        return filename
    }
    filePrefix?.let {
        return if (it.endsWith("/")) {
            "$it$filename"
        } else {
            "$it/$filename"
        }
    }
    return null
}

fun ChapterBuilder?.addPrefixIfNotNull(filenames: Array<String>): Array<String> {
    if (filenames.isEmpty()) {
        return emptyArray()
    }
    if (this?.filePrefix == null) {
        return filenames
    }
    filePrefix?.let {
        return filenames.map {filename ->
            if (it.endsWith("/")) {
                "$it$filename"
            } else {
                "$it/$filename"
            }
        } .toTypedArray()

    }
    return emptyArray()
}

data class ChapterBuilder(
        var id: String? = null,
        var name: String? = null,
        var categories: Array<String> = emptyArray(),
        var theme: String? = null,
        val flagGroups: MutableList<FlagGroup> = mutableListOf(),
        var states: MutableList<State> = mutableListOf(),
        var testDefaultTheme: String? = null,
        var filePrefix: String? = null
) : LineParser {
    override fun parseLine(line: String, index: Int) {
        when {
            line.matches(IMAGE_PREFIX) -> {
                filePrefix = line.parameterValue()
            }
            line.matches(FLAG_GROUP) -> {
                val groupName = line.replace(FLAG_GROUP, "$1")
                val groupType = line.replace(FLAG_GROUP, "$2")
                val type = when (groupType) {
                    "prave_jeden" -> FlagType.EXACTLY_ONE
                    "aspon_jeden" -> FlagType.AT_LEAST_ONE
                    else -> {
                        throw IncorrectLineException(msg = "Invalid flag type: $groupType", line = line, index = index)
                    }
                }
                val flags = line.replace(FLAG_GROUP, "$3").split(",").map { it.trim() }.toTypedArray()
                flagGroups.add(FlagGroup(groupName, type = type, flags = flags))
            }
            line.matches(GameConverter.KATEGORIE) -> {
                categories = line.parameterArray()
            }
            line.matches(GameConverter.TEMA) -> {
                theme = line.replace(GameConverter.TEMA, "$1")
            }
            line.matches(TEST_DEFAULT_TEMA) -> {
                testDefaultTheme = line.replace(TEST_DEFAULT_TEMA, "$1")
            }
            else -> {
                errorIfParameter(line, index)
                name(line)
            }
        }
    }

    fun build(): Chapter {
        return Chapter(
                id = id!!,
                name = name ?: "",
                categories = categories,
                theme = theme,
                flagGroups = flagGroups.toTypedArray(),
                states = states.toTypedArray(),
                testDefaultTheme = testDefaultTheme,
                filePrefix = filePrefix
        )
    }

    fun name(value: String) {
        name = value
    }

    fun addStates(states: List<State>) {
        this.states.addAll(states)
    }

    fun addState(state: State) {
        this.states.add(state)
    }

    companion object {
        val IMAGE_PREFIX = "obrazky_prefix=(.*)".toRegex()

        val TEST_DEFAULT_TEMA = "test_zakladni_tema=([a-zA-Z\\-0-9_{}]+)".toRegex()

        val FLAG_GROUP = "skupina\\(([0-9A-Za-z_-]*),(prave_jeden|aspon_jeden)\\)=(.*)".toRegex()

    }
}