package eu.karelhovorka.textgame.core.bo

import eu.karelhovorka.textgame.core.builder.GameConverter

data class GameConfig(
        val startId: String,
        val states: Array<State>,
        val moves: Array<Move>,
        val chapters: Array<Chapter>
) {

    companion object {
        fun fromText(text: String): GameConfig {
            return GameConverter().convert(text)
        }

        fun fromStates(states: Array<State>): GameConfig {
            val firstState = states.first()
            val chapter = Chapter(id = firstState.chapterId, name = firstState.chapterId, states = states)
            val moves = mutableListOf<Move>()
            states.forEach { moves.addAll(it.moves) }
            return GameConfig(states.first().id, states, moves.toTypedArray(), chapters = arrayOf(chapter))
        }
    }

}
