package eu.karelhovorka.textgame.core.builder

import eu.karelhovorka.textgame.core.bo.Audio
import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.PauseType
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.builder.GameConverter.Companion.HRANA
import eu.karelhovorka.textgame.core.validator.STATE_ID_REGEX
import eu.karelhovorka.textgame.core.variables.Variable

enum class Selection(val text: String) {
    RANDOM("nahodny"),
    FIRST_VALID("prvni_vyhovujici"),
    NONE("zadny");

    companion object {

        fun fromText(text_: String): Selection {
            for (value in values()) {
                if (value.text == text_) {
                    return value
                }
            }
            return NONE
        }
    }
}

data class StateBuilder(
        val gameConverterContext: GameConverterContext,
        var id: String? = null,
        var illustration: String? = null,
        private var text_: String? = null,
        var flags: MutableList<String> = mutableListOf(),
        var flagsToRemove: MutableList<String> = mutableListOf(),
        var moveIds: MutableList<String> = mutableListOf(),
        var chapter: ChapterBuilder? = null,
        var chapterId: String? = null,
        var categories: Array<String> = emptyArray(),
        var endType: EndType = EndType.NONE,
        var theme: String? = null,
        var background: Array<String> = emptyArray(),
        var minigame: String? = null,
        var variables: MutableList<Variable> = mutableListOf<Variable>(),
        //for superstate
        var moves: MutableList<MoveBuilder> = mutableListOf(),
        var checkpoint: String? = null,
        var selfConsuming: Boolean? = null,
        var selection: Selection = Selection.NONE,
        var pause: PauseType = PauseType.AUTO,
        var audio: Array<Audio> = emptyArray()

) : LineParser {

    init {

        if (chapterId != null && chapter == null) {
            chapter = ChapterBuilder(id = chapterId)
        }
    }

    var parsingMoves = false

    override fun parseLine(line: String, index: Int) {
        if (line.matches(HRANA)) {
            val moveId: String? = line.moveId()
            if (moveId != null) {
                gameConverterContext.addMoveId(moveId, index)
            }
            val move = addMove(moveId)
            val targetId: String = line.parameterValue()
            if (targetId.isNotBlank() && targetId.matches(STATE_ID_REGEX)) {
                move.targetId = targetId
            }
            val moveType = line.moveType()
            if (moveType == MoveType.LEFT_ARROW) {
                move.targetId = id
            }
            parsingMoves = true
        } else if (parsingMoves) {
            currentMoveBuilder().parseLine(line, index)
        } else {
            parseStateLine(line, index)
        }
    }

    private fun parseStateLine(line: String, index: Int) {
        when {
            line.matches(GameConverter.ILUSTRACE) -> {
                illustration = line.parameterValue()
            }
            line.matches(GameConverter.PAUZA) -> {
                pause = eu.karelhovorka.textgame.core.builder.pause(line, index)
            }
            line.matches(GameConverter.PROMENNA) -> {
                variables.add(line.parameterVariable())
            }
            line.matches(GameConverter.PRIZNAK) -> {
                flags = line.parameterArray().toMutableList()
            }
            line.matches(GameConverter.ODSTRANIT_PRIZNAK) -> {
                flagsToRemove = line.parameterArray().toMutableList()
            }
            line.matches(VYBER) -> {
                selection = Selection.fromText(line.parameterValue())
            }
            line.matches(GameConverter.POZIRACI) -> {
                selfConsuming = line.parameterBoolean()
            }
            line.matches(MINIHRA) -> {
                minigame = line.parameterValue()
            }
            line.matches(GameConverter.POZADI) -> {
                background = line.parameterArray()
            }
            line.matches(CHECKPOINT) -> {
                checkpoint = line.parameterValue()
            }
            line.matches(GameConverter.TEMA) -> {
                theme = line.replace(GameConverter.TEMA, "$1")
            }
            line.matches(GameConverter.CIL) -> {
                val targetId = line.parameterValue()
                val moveId = "h_${id}_auto_$targetId"
                gameConverterContext.addMoveId(moveId, index)
                moves.add(MoveBuilder(
                        gameConverterContext = gameConverterContext,
                        id = moveId,
                        targetId = targetId,
                        selfConsuming = selfConsuming ?: MoveBuilder.DEFAULT_SELF_CONSUMING,
                        categories = categories,
                        background = background
                ))
            }
            line.matches(GameConverter.KONEC) -> {
                endType = endType(line, index)
            }
            line.matches(GameConverter.KATEGORIE) -> {
                categories = line.parameterArray()
            }
            else -> {
                errorIfParameter(line, index)
                appendText(line)
            }
        }
    }

    fun currentMoveBuilder(): MoveBuilder {
        return moves.last()
    }

    fun addMove(moveId: String? = null): MoveBuilder {
        val moveBuilder = MoveBuilder(
                gameConverterContext = gameConverterContext,
                id = moveId,
                selfConsuming = selfConsuming ?: MoveBuilder.DEFAULT_SELF_CONSUMING,
                categories = categories,
                background = background,
                chapter = chapter
        )
        moves.add(moveBuilder)
        return moveBuilder
    }

    var stringBuilder = CharSequenceBuilder()

    var text: CharSequence? = text_
        set(value) {
            if (value != null && value.contains("@")) {
                field = CharSequenceBuilder().appendFullText(value).build()
            } else {
                field = value
            }
        }


    fun build(nextStateId: String? = null): State {
        var finalText: CharSequence? = stringBuilder.build()
        if (text != null) {
            finalText = text
        }
        if (nextStateId != null) {
            moves.forEach {
                if (it.targetId == null) {
                    it.targetId = nextStateId
                }
            }
        }
        var moveIndex = 0
        moves.forEach {
            if (it.id == null) {
                val moveId = "h_${id}_m${moveIndex}"
                it.id = moveId
            }
            moveIndex += 1
        }
        return State(
                id = id!!,
                illustration = chapter.addPrefixIfNotNull(illustration),
                text = finalText?.trim(),
                chapterId = chapter?.id ?: chapterId!!,
                flags = flags.toTypedArray(),
                flagsToRemove = flagsToRemove.toTypedArray(),
                moves = moves.map { it.build() }.toTypedArray(),
                categories = categories,
                endType = endType,
                theme = theme,
                background = chapter.addPrefixIfNotNull(background),
                minigame = minigame,
                variables = variables.toTypedArray(),
                checkpoint = checkpoint,
                selection = selection,
                pause = pause
        )
    }

    fun text(init: CharSequenceBuilder.() -> Unit): CharSequenceBuilder {
        stringBuilder = CharSequenceBuilder()
        stringBuilder.init()
        return stringBuilder
    }

    fun appendText(value: String) {
        stringBuilder.appendLine(value)
    }

    companion object {
        val MINIHRA = "minihra=(.*)".toRegex()

        val CHECKPOINT = "zachytny_bod=(.*)".toRegex()

        val VYBER = "autovyber=(nahodny|prvni_vyhovujici|zadny)".toRegex()
    }

}