package eu.karelhovorka.textgame.core.validator

import eu.karelhovorka.textgame.core.bo.GameConfig
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State

class FlagAnalyser(val gameConfig: GameConfig) {


    fun run(): Map<String, StatesAndMoves> {
        val statesOfFlag = gameConfig.states.flatMap { state ->
            state.flags.map { flag ->
                flag to state
            }
        }.groupBy({ it.first }, { it.second })
        val movesOfFlag = gameConfig.moves.flatMap { state ->
            state.flags.map { flag ->
                flag to state
            }
        }.groupBy({ it.first }, { it.second })
        val allFlags = movesOfFlag.keys + statesOfFlag.keys
        return allFlags.map {
            it to StatesAndMoves(statesOfFlag[it]?.toTypedArray() ?: emptyArray(), movesOfFlag[it]?.toTypedArray()
                    ?: emptyArray())
        }.toMap()
    }

    override fun toString(): String {
        return run().map { "${it.key} -> ${it.value}" }.joinToString("\n")
    }

    data class StatesAndMoves(val states: Array<State>, val moves: Array<Move>) {
        override fun toString(): String {
            val statesString = if (states.isEmpty()) {
                ""
            } else {
                "U: ${states.map { it.id }.toList()}"
            }
            val movesString = if (moves.isEmpty()) {
                ""
            } else {
                "H: ${moves.map { it.id }.toList()}"
            }
            return "$statesString $movesString"
        }
    }
}